              <p>我热爱艺术创作，专注于绘画与摄影领域。对我而言，艺术不仅是表达自我情感的方式，更是记录生活美好瞬间的重要途径。</p>
              <p>在这个个人空间里，我分享自己的创作历程、生活感悟和艺术心得，希望能够通过作品与更多人产生共鸣。</p>
            </div>
            
            <div class="personal-info">
              <div class="info-item">
                <TIcon name="user" />
                <span>孙可馨</span>
              </div>
              <div class="info-item">
                <TIcon name="location" />
                <span>武汉理工大学</span>
              </div>
              <div class="info-item">
                <TIcon name="book" />
                <span>经济2403班</span>
              </div>
              <div class="info-item">
                <TIcon name="heart" />
                <span>绘画、摄影、运动、手工</span>
              </div>
            </div>
          </div>
          
          <div class="intro-image">
            <img 
              :src="profileImage" 
              alt="孙可馨的个人头像" 
              class="intro-photo"
            />
          </div>
        </div>
      </section>

      <!-- 技能与专长 -->
      <section class="skills">
        <h2 class="section-title">技能与专长</h2>
        <div class="skills-grid">
          <div v-for="skill in skills" :key="skill.name" class="skill-card">
