            <p>{{ skill.description }}</p>
            <div class="skill-level">
              <div class="level-bar">
                <div class="level-fill" :style="{ width: skill.level + '%' }"></div>
              </div>
              <span>{{ skill.level }}%</span>
            </div>
          </div>
        </div>
      </section>

      <!-- 教育背景与联系方式 -->
      <div class="info-sections">
        <section class="education">
          <h2 class="section-title">教育背景</h2>
          <div class="timeline">
          <div class="timeline-item">
            <div class="timeline-marker">
              <TIcon name="book" />
            </div>
            <div class="timeline-content">
              <h3>武汉理工大学</h3>
              <p class="timeline-period">2024年9月 - 至今</p>
              <p class="timeline-description">经济专业，主修经济学等课程，深入学习经济理论与实务应用。</p>
            </div>
          </div>
          </div>
        </section>

        <!-- 联系方式 -->
        <section class="contact">
          <h2 class="section-title">联系我</h2>
          <div class="contact-content">
            <p>欢迎与我交流艺术创作、分享创意想法，或者探讨合作机会！</p>
            <div class="contact-methods">
              <div class="contact-item" @click="copyContact('email')">
                <TIcon name="mail" />
                <div class="contact-info">
                  <h4>邮箱</h4>
                  <p>1783073686@qq.com</p>
                </div>
              </div>
              <div class="contact-item" @click="copyContact('wechat')">
                <TIcon name="chat" />
                <div class="contact-info">
                  <h4>微信</h4>
                  <p>13154715568</p>
                </div>
              </div>
              <div class="contact-item" @click="openSocial('instagram')">
                <TIcon name="image" />
                <div class="contact-info">
                  <h4>Instagram</h4>
                  <p>@sun_kexin_art</p>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>

      <!-- 留言板 -->
      <section class="guestbook">
