          />
          <div class="message-actions">
            <TInput
              v-model="visitorName"
              placeholder="你的昵称（可选）"
              class="name-input"
              maxlength="20"
            />
            <TButton 
              theme="primary" 
              @click="submitMessage"
              :disabled="!newMessage.trim()"
              class="submit-btn"
            >
              发布留言
            </TButton>
          </div>
        </div>

        <!-- 留言列表 -->
        <div class="messages-list" v-if="messages.length > 0">
          <div 
            v-for="message in messages" 
            :key="message.id" 
            class="message-item"
          >
