              {{ message.content }}
            </div>
            <div class="message-footer">
              <TButton 
                variant="text" 
                size="small"
                @click="toggleReply(message.id)"
                class="reply-btn"
              >
                <TIcon name="chat" />
                回复 ({{ message.replies.length }})
              </TButton>
            </div>

            <!-- 回复区域 -->
            <div v-if="message.showReply" class="reply-section">
