                <TInput
                  v-model="replyContents[message.id]"
                  :placeholder="`回复 ${message.name || '匿名用户'}...`"
                  @keyup.enter="submitReply(message.id)"
                  class="reply-field"
                />
                <TButton 
                  theme="primary" 
                  size="small"
                  @click="submitReply(message.id)"
                  :disabled="!replyContents[message.id]?.trim()"
                >
                  回复
                </TButton>
              </div>

