                    <TAvatar :image="reply.avatar" size="small" />
                    <span class="reply-username">{{ reply.name || '匿名用户' }}</span>
                    <span class="reply-time">{{ formatTime(reply.timestamp) }}</span>
                  </div>
                  <div class="reply-content">
                    {{ reply.content }}
                  </div>
