</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import { 
  MessagePlugin, 
  Icon as TIcon, 
  Button as TButton, 
  Input as TInput, 
  Textarea as TTextarea,
  Avatar as TAvatar
} from 'tdesign-vue-next'
import profileImage from '@/assets/images/avatar/profile.jpg'

// 留言数据接口
interface Message {
  id: string
  name: string
  content: string
  timestamp: number
  avatar: string
  replies: Reply[]
  showReply: boolean
}

interface Reply {
  id: string
  name: string
  content: string
  timestamp: number
  avatar: string
}

const skills = ref([
  {
    name: '绘画',
    description: '水彩画、素描、速写等传统绘画技艺',
    icon: 'edit-1',
    level: 90
  },
  {
    name: '摄影',
    description: '风景、人像、静物摄影，熟练掌握后期处理',
    icon: 'camera',
    level: 85
  },
  {
    name: '运动',
    description: '跑步、游泳等运动项目，保持健康生活',
    icon: 'stars',
    level: 80
  },
  {
    name: '手工',
    description: '手工制作、DIY创意、手工艺设计',
    icon: 'palette',
    level: 75
  }
])

// 留言相关数据
const messages = ref<Message[]>([])
const newMessage = ref('')
const visitorName = ref('')
const replyContents = ref<Record<string, string>>({})

// 生成随机头像
const generateAvatar = (name?: string) => {
  const seed = name || Math.random().toString(36).substring(7)
  return `https://api.dicebear.com/7.x/avataaars/svg?seed=${seed}`
}

// 从本地存储加载留言
const loadMessages = () => {
  const saved = localStorage.getItem('guestbook_messages')
  if (saved) {
    messages.value = JSON.parse(saved)
  }
}

// 保存留言到本地存储
const saveMessages = () => {
  localStorage.setItem('guestbook_messages', JSON.stringify(messages.value))
}

// 发布留言
const submitMessage = () => {
  if (!newMessage.value.trim()) return

  const message: Message = {
    id: Date.now().toString(),
    name: visitorName.value.trim() || '',
    content: newMessage.value.trim(),
    timestamp: Date.now(),
    avatar: generateAvatar(visitorName.value.trim()),
    replies: [],
    showReply: false
  }

  messages.value.unshift(message)
  saveMessages()
  
  // 重置表单
  newMessage.value = ''
  visitorName.value = ''
  
  MessagePlugin.success('留言发布成功！')
}

// 切换回复区域显示
const toggleReply = (messageId: string) => {
  const message = messages.value.find(m => m.id === messageId)
  if (message) {
    message.showReply = !message.showReply
    if (!message.showReply) {
      delete replyContents.value[messageId]
    }
  }
}

// 提交回复
const submitReply = (messageId: string) => {
  const content = replyContents.value[messageId]?.trim()
  if (!content) return

  const message = messages.value.find(m => m.id === messageId)
  if (!message) return

  const reply: Reply = {
    id: Date.now().toString(),
    name: visitorName.value.trim() || '',
    content: content,
    timestamp: Date.now(),
    avatar: generateAvatar(visitorName.value.trim())
  }

  message.replies.push(reply)
  saveMessages()
  
  // 清空回复输入
  replyContents.value[messageId] = ''
  
  MessagePlugin.success('回复成功！')
}

// 格式化时间
const formatTime = (timestamp: number) => {
  const date = new Date(timestamp)
  const now = new Date()
  const diff = now.getTime() - date.getTime()
  
  if (diff < 60000) { // 1分钟内
    return '刚刚'
  } else if (diff < 3600000) { // 1小时内
    return `${Math.floor(diff / 60000)}分钟前`
  } else if (diff < 86400000) { // 1天内
    return `${Math.floor(diff / 3600000)}小时前`
  } else if (diff < 604800000) { // 1周内
    return `${Math.floor(diff / 86400000)}天前`
  } else {
    return date.toLocaleDateString('zh-CN', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }
}



const copyContact = (type: string) => {
  if (type === 'email') {
    navigator.clipboard.writeText('1783073686@qq.com')
    MessagePlugin.success('邮箱已复制到剪贴板')
  } else if (type === 'wechat') {
    navigator.clipboard.writeText('13154715568')
    MessagePlugin.success('微信号已复制到剪贴板')
  }
}

const openSocial = (platform: string) => {
  console.log('Open social platform:', platform)
}

// 页面加载时获取留言
onMounted(() => {
  loadMessages()
})
</script>

